import React, { useState, useEffect } from 'react';
import { db } from '../firebase/firebase';
import { collection, getDocs, addDoc, updateDoc, doc, deleteDoc } from 'firebase/firestore';
import './InventoryManagement.css';

function InventoryManagement() {
  const [items, setItems] = useState([]);
  const [newItem, setNewItem] = useState({ id: '', name: '', price: 0, quantity: 0 });
  const nysSalesTaxRate = 0.08875;

  useEffect(() => {
    fetchItems();
  }, []);

  const fetchItems = async () => {
    const querySnapshot = await getDocs(collection(db, 'inventory'));
    const fetchedItems = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
    // Sort items alphabetically by name
    fetchedItems.sort((a, b) => a.name.localeCompare(b.name));
    setItems(fetchedItems);
  };

  const handleAddOrUpdate = async () => {
    if (newItem.id) {
      // Update existing item
      await updateDoc(doc(db, 'inventory', newItem.id), {
        name: newItem.name,
        price: newItem.price,
        quantity: newItem.quantity,
      });
    } else {
      // Add new item
      await addDoc(collection(db, 'inventory'), {
        name: newItem.name,
        price: newItem.price,
        quantity: newItem.quantity,
      });
    }
    fetchItems();
    setNewItem({ id: '', name: '', price: 0, quantity: 0 });
  };

  const handleEdit = (item) => {
    setNewItem(item); // Populate the form with the selected item's details
  };

  const handleDelete = async (id) => {
    await deleteDoc(doc(db, 'inventory', id));
    fetchItems();
  };

  const handleUpdateQuantity = async (id, change) => {
    const item = items.find(i => i.id === id);
    await updateDoc(doc(db, 'inventory', id), { quantity: item.quantity + change });
    fetchItems();
  };

  // Calculate the total price of all inventory items
  const totalInventoryPrice = items.reduce((total, item) => total + item.price * item.quantity, 0);

  return (
    <div className="inventory-management">
      <h2>Inventory Management</h2>
      <div className="form">
        <label>Name:</label>
        <input
          value={newItem.name}
          onChange={(e) => setNewItem({ ...newItem, name: e.target.value })}
        />
        <label>Price:</label>
        <input
          type="number"
          value={newItem.price}
          onChange={(e) => setNewItem({ ...newItem, price: parseFloat(e.target.value) })}
        />
        <label>Quantity:</label>
        <input
          type="number"
          value={newItem.quantity}
          onChange={(e) => setNewItem({ ...newItem, quantity: parseInt(e.target.value) })}
        />
        <button onClick={handleAddOrUpdate}>
          {newItem.id ? 'Update Item' : 'Add Item'}
        </button>
      </div>
      <div className="inventory-list two-columns">
        {items.map(item => {
          const salesTax = (item.price * nysSalesTaxRate).toFixed(2);
          return (
            <div key={item.id} className="inventory-item">
              <span>{item.name} - ${item.price}</span>
              <span>NYS Sales Tax: ${salesTax}</span>
              <span>Qty: {item.quantity}</span>
              <button onClick={() => handleUpdateQuantity(item.id, 1)}>+</button>
              <button onClick={() => handleUpdateQuantity(item.id, -1)}>-</button>
              <button onClick={() => handleEdit(item)}>Edit</button>
              <button onClick={() => handleDelete(item.id)}>Delete</button>
            </div>
          );
        })}
      </div>
      {/* Display the total price below the inventory list */}
      <div className="total-price">
        <h3>Total Inventory Value: ${totalInventoryPrice.toFixed(2)}</h3>
      </div>
    </div>
  );
}

export default InventoryManagement;