import React, { useState, useEffect } from 'react';
import { db } from '../firebase/firebase';
import { collection, getDocs, deleteDoc, doc } from 'firebase/firestore';
import './Reports.css';

function Reports() {
  const [sales, setSales] = useState([]);
  const [filteredSales, setFilteredSales] = useState([]);
  const [instructorPayments, setInstructorPayments] = useState([]);
  const [filteredInstructorPayments, setFilteredInstructorPayments] = useState([]);
  const [inventorySales, setInventorySales] = useState([]);
  const [expenses, setExpenses] = useState([]);
  const [filteredExpenses, setFilteredExpenses] = useState([]);
  const [period, setPeriod] = useState('day');
  const [selectedMonth, setSelectedMonth] = useState(new Date().getMonth() + 1);
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());

  useEffect(() => {
    fetchData();
  }, []);

  useEffect(() => {
    filterDataByPeriod();
  }, [sales, instructorPayments, expenses, period, selectedMonth, selectedYear]);

  const fetchData = async () => {
    try {
      const salesSnapshot = await getDocs(collection(db, 'sales'));
      setSales(salesSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
      const paymentsSnapshot = await getDocs(collection(db, 'instructorPayments'));
      setInstructorPayments(paymentsSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
      const inventorySalesSnapshot = await getDocs(collection(db, 'inventorySales'));
      setInventorySales(inventorySalesSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
      const expensesSnapshot = await getDocs(collection(db, 'expenses'));
      setExpenses(expensesSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  };

  const filterDataByPeriod = () => {
    const now = new Date();
    const filterByPeriod = (data) => {
      return data.filter(d => {
        const date = new Date(d.date);
        if (period === 'day') return date.toDateString() === now.toDateString();
        if (period === 'week') {
          const startOfWeek = new Date(now);
          startOfWeek.setDate(now.getDate() - now.getDay());
          const endOfWeek = new Date(startOfWeek);
          endOfWeek.setDate(startOfWeek.getDate() + 6);
          return date >= startOfWeek && date <= endOfWeek;
        }
        if (period === 'month') return date.getMonth() + 1 === selectedMonth && date.getFullYear() === selectedYear;
        if (period === 'year') return date.getFullYear() === selectedYear;
        return true;
      });
    };

    setFilteredSales(filterByPeriod(sales));
    setFilteredInstructorPayments(filterByPeriod(instructorPayments));
    setFilteredExpenses(filterByPeriod(expenses));
  };

  const groupSalesByDate = (sales) => {
    return sales.reduce((groups, sale) => {
      const date = new Date(sale.date);
      const localDateString = new Date(date.getTime() + date.getTimezoneOffset() * 60000).toLocaleDateString('en-US', {
        timeZone: 'America/New_York', // Use the appropriate time zone
      });
      if (!groups[localDateString]) {
        groups[localDateString] = [];
      }
      groups[localDateString].push(sale);
      return groups;
    }, {});
  };

  const groupedSales = groupSalesByDate(filteredSales);

  const membershipSales = filteredSales.filter(s => s.item.includes('Membership'));
  const nonMembershipSales = filteredSales.filter(s => !s.item.includes('Membership'));
  const now = new Date();
  const filteredInventorySales = inventorySales.filter(s => {
    const date = new Date(s.date);
    if (period === 'day') return date.toDateString() === now.toDateString();
    if (period === 'week') {
      const startOfWeek = new Date(now);
      startOfWeek.setDate(now.getDate() - now.getDay());
      const endOfWeek = new Date(startOfWeek);
      endOfWeek.setDate(startOfWeek.getDate() + 6);
      return date >= startOfWeek && date <= endOfWeek;
    }
    if (period === 'month') return date.getMonth() + 1 === selectedMonth && date.getFullYear() === selectedYear;
    if (period === 'year') return date.getFullYear() === selectedYear;
    return true;
  });
  const instructorCosts = filteredInstructorPayments.filter(s => {
    const date = new Date(s.date);
    if (period === 'day') return date.toDateString() === now.toDateString();
    if (period === 'week') {
      const startOfWeek = new Date(now);
      startOfWeek.setDate(now.getDate() - now.getDay());
      const endOfWeek = new Date(startOfWeek);
      endOfWeek.setDate(startOfWeek.getDate() + 6);
      return date >= startOfWeek && date <= endOfWeek;
    }
    if (period === 'month') return date.getMonth() + 1 === selectedMonth && date.getFullYear() === selectedYear;
    if (period === 'year') return date.getFullYear() === selectedYear;
    return true;
  });

  const totals = {
    membership: Number(membershipSales.reduce((sum, s) => sum + s.amount, 0).toFixed(2)),
    nonMembership: Number(nonMembershipSales.reduce((sum, s) => sum + s.amount, 0).toFixed(2)),
    inventory: Number(filteredInventorySales.reduce((sum, s) => sum + (s.amount - s.tax), 0).toFixed(2)),
    tax: Number(filteredInventorySales.reduce((sum, s) => sum + s.tax, 0).toFixed(2)),
    instructor: Number(instructorCosts.reduce((sum, p) => sum + p.amount, 0).toFixed(2)),
    totalInventorySales: Number(inventorySales.reduce((sum, s) => sum + s.amount, 0).toFixed(2)),
    expenses: Number(filteredExpenses.reduce((sum, e) => sum + e.cost, 0).toFixed(2))
  };

  const nySalesTaxRate = .08875; // New York State sales tax rate
  const nySalesTax = Number((totals.nonMembership * nySalesTaxRate).toFixed(2));
  const totalSales = totals.membership + totals.nonMembership;
  const finalTotal = totalSales - totals.expenses - totals.instructor;

  const handleDelete = async (id, collectionName) => {
    if (window.confirm('Are you sure you want to delete this item?')) {
      await deleteDoc(doc(db, collectionName, id));
      fetchData();
    }
  };

  return (
    <div className="reports">
      <h2>Reports</h2>
      <select value={period} onChange={(e) => setPeriod(e.target.value)}>
        <option value="day">Day</option>
        <option value="week">Week</option>
        <option value="month">Month</option>
        <option value="year">Year</option>
      </select>
      {(period === 'month' || period === 'year') && (
        <>
          <label>Select Month:</label>
          <select value={selectedMonth} onChange={(e) => setSelectedMonth(parseInt(e.target.value))}>
            {Array.from({ length: 12 }, (_, i) => i + 1).map(month => (
              <option key={month} value={month}>{new Date(0, month - 1).toLocaleString('default', { month: 'long' })}</option>
            ))}
          </select>
          <label>Select Year:</label>
          <select value={selectedYear} onChange={(e) => setSelectedYear(parseInt(e.target.value))}>
            {Array.from({ length: 10 }, (_, i) => new Date().getFullYear() - i).map(year => (
              <option key={year} value={year}>{year}</option>
            ))}
          </select>
        </>
      )}
      <h3>Sales Details</h3>
      <div className="sales-list two-columns">
        {Object.keys(groupedSales).map(date => (
          <div key={date}>
            <h4>{date}</h4>
            {groupedSales[date].map(s => (
              <div key={s.id} className="sales-item">
                <span>{s.studentName} - {s.item} - ${s.amount.toFixed(2)}</span>
                <button onClick={() => handleDelete(s.id, 'sales')}>Delete</button>
              </div>
            ))}
          </div>
        ))}
      </div>
      <p>Total Membership Sales: ${totals.membership}</p>
      <p>Total Non-Membership Sales: ${totals.nonMembership}</p>
      <p>New York State Sales Tax on Non-Membership Sales: ${nySalesTax}</p>
      <p>Total Sales: ${totalSales.toFixed(2)}</p>
      <h3>Instructor Payments</h3>
      <ul>
        {filteredInstructorPayments.map(p => (
          <li key={p.id}>{p.instructor} - ${p.amount.toFixed(2)} - {new Date(p.date).toLocaleDateString()} <button onClick={() => handleDelete(p.id, 'instructorPayments')}>Delete</button></li>
        ))}
      </ul>
      <p>Total Instructor Payments: ${totals.instructor}</p>
      <h3>Expenses</h3>
<ul>
  {filteredExpenses
    .sort((a, b) => new Date(b.date) - new Date(a.date)) // Sort expenses by date (most recent first)
    .map((expense, index) => (
      <li key={index}>
        {expense.date} - {expense.item}: ${expense.cost.toFixed(2)} ({expense.paymentMethod}) - Purchased From: {expense.purchasedFrom}
        <button onClick={() => handleDelete(expense.id, 'expenses')}>Delete</button>
      </li>
    ))}
</ul>
<p>Total Expenses: ${totals.expenses}</p>
      <h3>Final Total</h3>
      <p>Total Sales - Expenses - Instructor Payments: ${finalTotal.toFixed(2)}</p>
    </div>
  );
}

export default Reports;