import React, { useState, useEffect } from 'react';
import { db } from '../firebase/firebase';
import { collection, getDocs, addDoc } from 'firebase/firestore';

function PaymentSystem() {
  const [students, setStudents] = useState([]);
  const [selectedStudent, setSelectedStudent] = useState('');
  const [paymentType, setPaymentType] = useState('membership');
  const [program, setProgram] = useState('Kids');
  const [inventoryItems, setInventoryItems] = useState([]);
  const [selectedItem, setSelectedItem] = useState('');
  const [isRecurring, setIsRecurring] = useState(false);
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);
  const membershipPrices = { Kids: 99, Adult: 109, 'Wolves+': 199 };
  const NY_TAX_RATE = 0.08875;

  useEffect(() => {
    fetchStudents();
    fetchInventory();
  }, []);

  const fetchStudents = async () => {
    const querySnapshot = await getDocs(collection(db, 'students'));
    setStudents(querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
  };

  const fetchInventory = async () => {
    const querySnapshot = await getDocs(collection(db, 'inventory'));
    setInventoryItems(querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
  };

  const handleRecordPayment = async () => {
    const student = students.find(s => s.id === selectedStudent);
    const selectedInventoryItem = inventoryItems.find(i => i.id === selectedItem);
    const baseAmount = paymentType === 'membership' ? membershipPrices[program] : selectedInventoryItem.price;
    const tax = paymentType === 'membership' ? 0 : (baseAmount * NY_TAX_RATE);
    const totalAmount = baseAmount + tax;
    const sale = {
      studentName: `${student.firstName} ${student.lastName}`,
      item: paymentType === 'membership' ? `${program} Membership` : selectedInventoryItem.name,
      amount: Number(totalAmount.toFixed(2)),
      date: selectedDate,
      tax: Number(tax.toFixed(2)),
      recurring: isRecurring
    };
    await addDoc(collection(db, 'sales'), sale);
    alert('Payment recorded!');
  };

  return (
    <div className="payment-system">
      <h2>Payment System</h2>
      <label>Select Student:</label>
      <select value={selectedStudent} onChange={(e) => setSelectedStudent(e.target.value)}>
        <option value="">Choose Student</option>
        {students.map(s => <option key={s.id} value={s.id}>{s.firstName} {s.lastName}</option>)}
      </select>
      <label>Payment Type:</label>
      <select value={paymentType} onChange={(e) => setPaymentType(e.target.value)}>
        <option value="membership">Membership</option>
        <option value="inventory">Inventory</option>
      </select>
      {paymentType === 'membership' ? (
        <>
          <label>Program:</label>
          <select value={program} onChange={(e) => setProgram(e.target.value)}>
            {Object.keys(membershipPrices).map(p => <option key={p} value={p}>{p} - ${membershipPrices[p]}</option>)}
          </select>
        </>
      ) : (
        <>
          <label>Item:</label>
          <select value={selectedItem} onChange={(e) => setSelectedItem(e.target.value)}>
            <option value="">Select Item</option>
            {inventoryItems.map(i => <option key={i.id} value={i.id}>{i.name} - ${i.price}</option>)}
          </select>
        </>
      )}
      <label>Payment Frequency:</label>
      <select value={isRecurring} onChange={(e) => setIsRecurring(e.target.value === 'true')}>
        <option value={false}>One-Time</option>
        <option value={true}>Monthly</option>
      </select>
      <label>Select Date:</label>
      <input type="date" value={selectedDate} onChange={(e) => setSelectedDate(e.target.value)} />
      <button onClick={handleRecordPayment}>Record Payment</button>
    </div>
  );
}

export default PaymentSystem;