import React from 'react';

function EditStudentOverlay({ student, onSave, onClose, onChange }) {
  return (
    <div className="overlay">
      <div className="overlay-content">
        <h3>Edit Student Details</h3>
        <form onSubmit={onSave}>
          <label>First Name:</label>
          <input type="text" value={student.firstName} onChange={(e) => onChange({ ...student, firstName: e.target.value })} />
          
          <label>Last Name:</label>
          <input type="text" value={student.lastName} onChange={(e) => onChange({ ...student, lastName: e.target.value })} />
          
          <label>Email:</label>
          <input type="email" value={student.email} onChange={(e) => onChange({ ...student, email: e.target.value })} />
          
          <label>Program:</label>
          <input type="text" value={student.program} onChange={(e) => onChange({ ...student, program: e.target.value })} />
          
          <label>Registration Date:</label>
          <input type="date" value={student.registrationDate} onChange={(e) => onChange({ ...student, registrationDate: e.target.value })} />
          
          <label>Street:</label>
          <input type="text" value={student.street} onChange={(e) => onChange({ ...student, street: e.target.value })} />
          
          <label>City:</label>
          <input type="text" value={student.city} onChange={(e) => onChange({ ...student, city: e.target.value })} />
          
          <label>State:</label>
          <input type="text" value={student.state} onChange={(e) => onChange({ ...student, state: e.target.value })} />
          
          <label>Zip Code:</label>
          <input type="text" value={student.zipCode} onChange={(e) => onChange({ ...student, zipCode: e.target.value })} />
          
          <label>Current Belt:</label>
          <input type="text" value={student.currentBelt} onChange={(e) => onChange({ ...student, currentBelt: e.target.value })} />
          
          <label>Belt Promotion Date:</label>
          <input type="date" value={student.beltPromotionDate} onChange={(e) => onChange({ ...student, beltPromotionDate: e.target.value })} />
          
          <label>Emergency Contact:</label>
          <input type="text" value={student.emergencyContact} onChange={(e) => onChange({ ...student, emergencyContact: e.target.value })} />
          
          <label>Emergency Phone:</label>
          <input type="text" value={student.emergencyContactPhone} onChange={(e) => onChange({ ...student, emergencyContactPhone: e.target.value })} />
          
          <label>Notes:</label>
          <textarea value={student.notes} onChange={(e) => onChange({ ...student, notes: e.target.value })}></textarea>
          
          <label>Waiver Signed:</label>
          <input type="checkbox" checked={student.waiverSigned} onChange={(e) => onChange({ ...student, waiverSigned: e.target.checked })} />
          
          <button type="submit">Save</button>
          <button type="button" onClick={onClose}>Cancel</button>
        </form>
      </div>
    </div>
  );
}

export default EditStudentOverlay;