import React, { useState } from 'react';
import './VideoPlayer.css';

const videoData = {
  guard: [
    { file: '/Privates.mp4', date: '2025-03-07' },
    { file: '/guard2.mp4', date: '2025-03-06' }
  ],
  standing: [
    { file: '/standing1.mp4', date: '2025-03-05' },
    { file: '/standing2.mp4', date: '2025-03-04' }
  ],
  passing: [
    { file: '/passing1.mp4', date: '2025-03-03' },
    { file: '/passing2.mp4', date: '2025-03-02' }
  ],
  concepts: [
    { file: '/concept1.mp4', date: '2025-03-01' },
    { file: '/concept2.mp4', date: '2025-02-28' }
  ],
};

const VideoPlayer = () => {
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedVideo, setSelectedVideo] = useState('');

  const handleCategorySelect = (category) => {
    setSelectedCategory(category);
    setSelectedVideo('');
  };

  const handleVideoSelect = (video) => {
    setSelectedVideo(video);
  };

  return (
    <div className="video-player">
      <select onChange={(e) => handleCategorySelect(e.target.value)}>
        <option value="">Select Category</option>
        {Object.keys(videoData).map((category) => (
          <option key={category} value={category}>
            {category}
          </option>
        ))}
      </select>
      {selectedCategory && (
        <ul>
          {videoData[selectedCategory].map((video) => (
            <li key={video.file} onClick={() => handleVideoSelect(video.file)}>
              {video.file} ({video.date})
            </li>
          ))}
        </ul>
      )}
      {selectedVideo && (
        <div className="video-container">
          <video controls>
            <source src={selectedVideo} type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        </div>
      )}
    </div>
  );
};

export default VideoPlayer;