import React, { useState } from 'react';
import { db } from '../firebase/firebase';
import { collection, addDoc } from 'firebase/firestore';

function Expenses() {
  const [item, setItem] = useState('');
  const [cost, setCost] = useState('');
  const [purchasedFrom, setPurchasedFrom] = useState(''); // New state for "Purchased From"
  const [date, setDate] = useState('');
  const [paymentMethod, setPaymentMethod] = useState('cash');
  const [message, setMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await addDoc(collection(db, 'expenses'), {
        item,
        cost: parseFloat(cost),
        purchasedFrom, // Include "Purchased From" in the Firestore document
        date,
        paymentMethod,
      });
      setMessage('Expense added successfully');
      setItem('');
      setCost('');
      setPurchasedFrom(''); // Reset "Purchased From" field
      setDate('');
      setPaymentMethod('cash');
    } catch (error) {
      console.error('Error adding document: ', error);
      setMessage('Error adding expense');
    }
  };

  return (
    <div className="expenses">
      <h2>Add Expense</h2>
      {message && <p>{message}</p>}
      <form onSubmit={handleSubmit}>
        <label>
          Item or Service:
          <input type="text" value={item} onChange={(e) => setItem(e.target.value)} required />
        </label>
        <label>
          Cost:
          <input type="number" value={cost} onChange={(e) => setCost(e.target.value)} required />
        </label>
        <label>
          Purchased From: {/* New field for "Purchased From" */}
          <input
            type="text"
            value={purchasedFrom}
            onChange={(e) => setPurchasedFrom(e.target.value)}
            required
          />
        </label>
        <label>
          Date:
          <input type="date" value={date} onChange={(e) => setDate(e.target.value)} required />
        </label>
        <label>
          Payment Method:
          <select value={paymentMethod} onChange={(e) => setPaymentMethod(e.target.value)}>
          <option value="card">Card</option>
            <option value="cash">Cash</option>
         
          </select>
        </label>
        <button type="submit">Add Expense</button>
      </form>
    </div>
  );
}

export default Expenses;