import React, { useState } from 'react';
import RegisterStudent from './components/RegisterStudent';
import StudentManagement from './components/StudentManagement';
import StudentCheckin from './components/StudentCheckin';
import Calendar from './components/Calendar';
import PaymentSystem from './components/PaymentSystem';
import InstructorManagement from './components/InstructorManagement';
import Expenses from './components/Expenses';
import InventoryManagement from './components/InventoryManagement';
import Reports from './components/Reports';
import Keypad from './components/Keypad';
import Newsletter from './components/Newsletter';
import VideoPlayer from './components/VideoPlayer'; // Import VideoPlayer component

import './App.css';

function App() {
  const [activeTab, setActiveTab] = useState('checkin');
  const [isUnlocked, setIsUnlocked] = useState(false);

  const handleUnlock = () => {
    setIsUnlocked(true);
  };

  return (
    <div className="App">
      <img src="/wolf mascot logo OCT2.png" alt="Logo" className="logo" /> 
      <h1>Wolves Academy of Jiu-Jitsu</h1>
      
      {isUnlocked ? (
        <>
          <nav>
            <button onClick={() => setActiveTab('register')}>Register Student</button>
            <button onClick={() => setActiveTab('management')}>Student Management</button>
            <button onClick={() => setActiveTab('checkin')}>Check-In</button>
            <button onClick={() => setActiveTab('calendar')}>Calendar</button>
            <button onClick={() => setActiveTab('payments')}>Payments</button>
            <button onClick={() => setActiveTab('instructors')}>Instructors</button>
            <button onClick={() => setActiveTab('expenses')}>Expenses</button>
            <button onClick={() => setActiveTab('inventory')}>Inventory</button>
            <button onClick={() => setActiveTab('reports')}>Reports</button>
            <button onClick={() => setActiveTab('newsletter')}>Newsletter</button>
            <button onClick={() => setActiveTab('videos')}>Videos</button> {/* Add Videos button */}
          </nav>
          <div className="content">
            {activeTab === 'register' && <RegisterStudent />}
            {activeTab === 'management' && <StudentManagement />}
            {activeTab === 'checkin' && <StudentCheckin />}
            {activeTab === 'calendar' && <Calendar />}
            {activeTab === 'payments' && <PaymentSystem />}
            {activeTab === 'instructors' && <InstructorManagement />}
            {activeTab === 'inventory' && <InventoryManagement />}
            {activeTab === 'expenses' && <Expenses />}
            {activeTab === 'reports' && <Reports />}
            {activeTab === 'newsletter' && <Newsletter />}
            {activeTab === 'videos' && <VideoPlayer />} {/* Render VideoPlayer component */}
          </div>
        </>
      ) : (
        <>
          <StudentCheckin />
          <Keypad onUnlock={handleUnlock} />
        </>
      )}
    </div>
  );
}

export default App;