import React, { useState } from 'react';
import { db } from '../firebase/firebase';
import { collection, addDoc } from 'firebase/firestore';

function RegisterStudent() {
  const [student, setStudent] = useState({
    firstName: '', lastName: '', email: '', phone: '', program: 'Adult', registrationDate: new Date().toISOString().split('T')[0],
    street: '', city: '', state: '', zipCode: '', currentBelt: 'White', beltPromotionDate: '',
    emergencyContact: '', emergencyContactPhone: '', notes: '', waiverSigned: false, attendance: []
  });

  const programs = ['Kids', 'Adult', 'Wolves+'];
  const adultBelts = ['White', 'Blue', 'Purple', 'Brown', 'Black'];
  const kidsBelts = ['White', 'Grey', 'Yellow', 'Orange', 'Green'];

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await addDoc(collection(db, 'students'), student);
      alert('Student registered!');
      setStudent({ ...student, firstName: '', lastName: '', email: '', phone: '', street: '', city: '', state: '', zipCode: '', emergencyContact: '', emergencyContactPhone: '', notes: '' });
    } catch (error) {
      console.error('Error adding document: ', error);
      alert('Error registering student: ' + error.message);
    }
  };

  return (
    <div className="register-student">
      <h2>Register Student</h2>
      <form onSubmit={handleSubmit} className="form-two-columns">
        <div className="column">
          <label>First Name:</label>
          <input value={student.firstName} onChange={(e) => setStudent({ ...student, firstName: e.target.value })} required />
          <label>Last Name:</label>
          <input value={student.lastName} onChange={(e) => setStudent({ ...student, lastName: e.target.value })} required />
          <label>Email:</label>
          <input type="email" value={student.email} onChange={(e) => setStudent({ ...student, email: e.target.value })} required />
          <label>Phone:</label>
          <input value={student.phone} onChange={(e) => setStudent({ ...student, phone: e.target.value })} required />
          <label>Program:</label>
          <select value={student.program} onChange={(e) => setStudent({ ...student, program: e.target.value })}>
            {programs.map(p => <option key={p} value={p}>{p}</option>)}
          </select>
          <label>Registration Date:</label>
          <input type="date" value={student.registrationDate} onChange={(e) => setStudent({ ...student, registrationDate: e.target.value })} required />
          <label>Street:</label>
          <input value={student.street} onChange={(e) => setStudent({ ...student, street: e.target.value })} required />
        </div>
        <div className="column">
          <label>City:</label>
          <input value={student.city} onChange={(e) => setStudent({ ...student, city: e.target.value })} required />
          <label>State:</label>
          <input value={student.state} onChange={(e) => setStudent({ ...student, state: e.target.value })} required />
          <label>Zip Code:</label>
          <input value={student.zipCode} onChange={(e) => setStudent({ ...student, zipCode: e.target.value })} required />
          <label>Current Belt:</label>
          <select value={student.currentBelt} onChange={(e) => setStudent({ ...student, currentBelt: e.target.value })}>
            {(student.program === 'Kids' ? kidsBelts : adultBelts).map(b => <option key={b} value={b}>{b}</option>)}
          </select>
          <label>Belt Promotion Date:</label>
          <input type="date" value={student.beltPromotionDate} onChange={(e) => setStudent({ ...student, beltPromotionDate: e.target.value })} />
          <label>Emergency Contact:</label>
          <input value={student.emergencyContact} onChange={(e) => setStudent({ ...student, emergencyContact: e.target.value })} required />
          <label>Emergency Phone:</label>
          <input value={student.emergencyContactPhone} onChange={(e) => setStudent({ ...student, emergencyContactPhone: e.target.value })} required />
          <label>Notes:</label>
          <textarea value={student.notes} onChange={(e) => setStudent({ ...student, notes: e.target.value })} />
          <label>Waiver Signed:</label>
          <input type="checkbox" checked={student.waiverSigned} onChange={(e) => setStudent({ ...student, waiverSigned: e.target.checked })} />
        </div>
        <button type="submit">Register</button>
      </form>
    </div>
  );
}

export default RegisterStudent;