import React, { useState, useEffect } from 'react';
import { db } from '../firebase/firebase';
import { collection, getDocs, doc, updateDoc, deleteDoc } from 'firebase/firestore';
import EditStudentOverlay from './EditStudentOverlay';
import StudentDetailsModal from './StudentDetailsModal';

function StudentManagement() {
  const [students, setStudents] = useState([]);
  const [selectedStudent, setSelectedStudent] = useState(null);
  const [showCheckins, setShowCheckins] = useState(false);
  const [editStudent, setEditStudent] = useState(null);

  useEffect(() => {
    fetchStudents();
  }, []);

  const fetchStudents = async () => {
    const querySnapshot = await getDocs(collection(db, 'students'));
    const studentData = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
    studentData.sort((a, b) => `${a.lastName} ${a.firstName}`.localeCompare(`${b.lastName} ${b.firstName}`));
    setStudents(studentData);
  };

  const handleDelete = async (id) => {
    if (window.confirm('Are you sure you want to delete this student?')) {
      await deleteDoc(doc(db, 'students', id));
      setStudents(students.filter(s => s.id !== id));
    }
  };

  const handleEditSubmit = async (e) => {
    e.preventDefault();
    await updateDoc(doc(db, 'students', editStudent.id), editStudent);
    fetchStudents();
    setEditStudent(null);
  };

  const handleStatusChange = async (student, status) => {
    await updateDoc(doc(db, 'students', student.id), { status });
    fetchStudents();
  };

  const getAttendanceStats = () => {
    const now = new Date();
    const today = now.toISOString().split('T')[0];
    const weekAgo = new Date(now.setDate(now.getDate() - 7)).toISOString().split('T')[0];
    const monthAgo = new Date(now.setMonth(now.getMonth() - 1)).toISOString().split('T')[0];

    const daily = students.reduce((sum, s) => sum + (s.attendance?.filter(a => a.date.split(' ')[0] === today).length || 0), 0);
    const weekly = students.reduce((sum, s) => sum + (s.attendance?.filter(a => a.date >= weekAgo).length || 0), 0);
    const monthly = students.reduce((sum, s) => sum + (s.attendance?.filter(a => a.date >= monthAgo).length || 0), 0);

    return { daily, weekly, monthly };
  };

  const stats = getAttendanceStats();

  const statusCounts = students.reduce((counts, student) => {
    counts[student.status] = (counts[student.status] || 0) + 1;
    return counts;
  }, {});

  const formatName = (name) => {
    return name.charAt(0).toUpperCase() + name.slice(1).toLowerCase();
  };

  return (
    <div className="student-management">
      <h2>Student Management</h2>
      <p>Daily Check-ins: {stats.daily} | Weekly: {stats.weekly} | Monthly: {stats.monthly}</p>
      <p>Active Students: {statusCounts['Active'] || 0}</p>
      <p>Paused Students: {statusCounts['Paused'] || 0}</p>
      <p>Non-Active Students: {statusCounts['Non-Active'] || 0}</p>
      <p>Payment Due Students: {statusCounts['Payment due'] || 0}</p>
      <p>Lead Students: {statusCounts['Lead'] || 0}</p>
      <div className="student-list two-columns">
        {students.map(s => (
          <div key={s.id} className="student-item">
            <span>{formatName(s.lastName)}, {formatName(s.firstName)} - {s.program}</span>
            <select value={s.status || 'Active'} onChange={(e) => handleStatusChange(s, e.target.value)}>
              <option value="Active">Active</option>
              <option value="Paused">Paused</option>
              <option value="Non-Active">Non-Active</option>
              <option value="Payment due">Payment due</option>
              <option value="Lead">Lead</option>
            </select>
            <button onClick={() => setSelectedStudent(s)}>View Details</button>
            <button onClick={() => setEditStudent(s)}>Edit</button>
            <button onClick={() => handleDelete(s.id)}>Delete</button>
          </div>
        ))}
      </div>
      {selectedStudent && (
        <StudentDetailsModal
          student={selectedStudent}
          onClose={() => setSelectedStudent(null)}
        />
      )}
      {editStudent && (
        <EditStudentOverlay
          student={editStudent}
          onSave={handleEditSubmit}
          onClose={() => setEditStudent(null)}
          onChange={setEditStudent}
        />
      )}
    </div>
  );
}

export default StudentManagement;