import React, { useState, useEffect } from 'react';
import { db } from '../firebase/firebase';
import { collection, getDocs, doc, updateDoc } from 'firebase/firestore';

function StudentCheckin() {
  const [students, setStudents] = useState([]);
  const [search, setSearch] = useState('');
  const [selectedStudent, setSelectedStudent] = useState(null);
  const [program, setProgram] = useState('Kids');
  const [message, setMessage] = useState('');
  const programs = ['Kids', 'Adult', 'Wolves+'];

  useEffect(() => {
    fetchStudents();
  }, []);

  const fetchStudents = async () => {
    const querySnapshot = await getDocs(collection(db, 'students'));
    const studentData = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
    setStudents(studentData);
  };

  const handleCheckin = async () => {
    if (!selectedStudent) return;

    // Check the student's status
    if (selectedStudent.status === 'Payment due' || selectedStudent.status === 'Non-Active' || selectedStudent.status === 'Paused') {
      setMessage('Student unable to sign-in. Please see front desk');
      return;
    }

    const time = new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    const date = `${new Date().toISOString().split('T')[0]} ${time}`;
    const updatedCheckins = [...(selectedStudent.checkins || []), { date, program }];
    
    try {
      await updateDoc(doc(db, 'students', selectedStudent.id), { checkins: updatedCheckins });
      setMessage(`${selectedStudent.firstName} ${selectedStudent.lastName} has successfully checked in`);
      fetchStudents();
      setSelectedStudent(null);
    } catch (error) {
      console.error('Error updating document: ', error);
      alert('Error checking in: ' + error.message);
    }
  };

  const handleNameClick = (student) => {
    setSelectedStudent(student);
    setMessage(`Check-in as ${student.firstName} ${student.lastName}`);
    const studentElement = document.getElementById(`student-${student.id}`);
    studentElement.classList.add('flash');
    setTimeout(() => {
      studentElement.classList.remove('flash');
    }, 500);
  };

  const handleClearSearch = () => {
    setSearch('');
  };

  const filteredStudents = students
    .filter(s => `${s.firstName} ${s.lastName}`.toLowerCase().includes(search.toLowerCase()))
    .slice(0, 2); // Limit to 2 students

  return (
    <div className="student-checkin">
      <h2>Student Check-In</h2> <h3>Type and click name.</h3>
      {message && <p className="message">{message}</p>}
      <div className="search-bar">
        <input type="text" value={search} onChange={(e) => setSearch(e.target.value)} placeholder="Search by name" />
        <button onClick={handleClearSearch}>Clear</button>
      </div>
      <ul>
        {filteredStudents.map(s => (
          <li key={s.id} id={`student-${s.id}`} onClick={() => handleNameClick(s)}>
            {s.firstName} {s.lastName} - {s.program}
          </li>
        ))}
      </ul>
      {selectedStudent && (
        <>
          <label>Program:</label>
          <select value={program} onChange={(e) => setProgram(e.target.value)}>
            {programs.map(p => <option key={p} value={p}>{p}</option>)}
          </select>
          <button onClick={handleCheckin}>Check In</button>
        </>
      )}
    </div>
  );
}

export default StudentCheckin;