import React, { useState } from 'react';
import './StudentDetailsModal.css';

function StudentDetailsModal({ student, onClose }) {
  const [showCheckins, setShowCheckins] = useState(false);

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <button className="close-button" onClick={onClose}>X</button>
        <h3>Student Details</h3>
        <div className="details-section">
          <div className="details-column">
            <h4>Student Information</h4>
            <p>First Name: {student.firstName}</p>
            <p>Last Name: {student.lastName}</p>
            <p>Email: {student.email}</p>
            <p>Program: {student.program}</p>
            <p>Registration Date: {student.registrationDate}</p>
            <p>Street: {student.street}</p>
            <p>City: {student.city}</p>
            <p>State: {student.state}</p>
            <p>Zip Code: {student.zipCode}</p>
            <p>Current Belt: {student.currentBelt}</p>
            <p>Belt Promotion Date: {student.beltPromotionDate}</p>
            <p>Emergency Contact: {student.emergencyContact}</p>
            <p>Emergency Phone: {student.emergencyContactPhone}</p>
            <p>Notes: {student.notes}</p>
            <p>Waiver Signed: {student.waiverSigned ? 'Yes' : 'No'}</p>
          </div>
          <div className="details-column">
            <h4>Attendance Information</h4>
            <button onClick={() => setShowCheckins(!showCheckins)}>
              {showCheckins ? 'Hide Check-ins' : 'Show Check-ins'}
            </button>
            {showCheckins && (
              <div className="checkin-window">
                {student.checkins?.map((a, i) => (
                  <p key={i}>{a.date} - {a.program}</p>
                ))}
                <p>Total Check-ins: {student.checkins?.length || 0}</p>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default StudentDetailsModal;