import React, { useState } from 'react';
import { db } from '../firebase/firebase';
import { collection, addDoc } from 'firebase/firestore';

function InstructorManagement() {
  const [instructor, setInstructor] = useState('Nick Boyer');
  const [amount, setAmount] = useState('');
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]); // Default to today's date
  const [note, setNote] = useState('');

  const handlePayment = async () => {
    const payment = { instructor, amount: Number(amount), date, note };
    await addDoc(collection(db, 'instructorPayments'), payment);
    alert('Payment recorded!');
    setAmount('');
    setNote('');
  };

  return (
    <div className="instructor-management">
      <h2>Instructor Management</h2>
      <label>Instructor:</label>
      <select value={instructor} onChange={(e) => setInstructor(e.target.value)}>
        <option value="Nick Boyer">Nick Boyer</option>
      </select>
      <label>Amount Paid:</label>
      <input type="number" value={amount} onChange={(e) => setAmount(e.target.value)} />
      <label>Date:</label>
      <input type="date" value={date} onChange={(e) => setDate(e.target.value)} />
      <label>Note:</label>
      <textarea value={note} onChange={(e) => setNote(e.target.value)} />
      <button onClick={handlePayment}>Record Payment</button>
    </div>
  );
}

export default InstructorManagement;