import React, { useState, useEffect } from 'react';
import Calendar from 'react-calendar';
import { db } from '../firebase/firebase';
import { collection, getDocs, doc, setDoc, deleteDoc } from 'firebase/firestore';
import 'react-calendar/dist/Calendar.css';

function CalendarComponent() {
  const [date, setDate] = useState(new Date());
  const [notes, setNotes] = useState({});
  const [currentNote, setCurrentNote] = useState('');

  useEffect(() => {
    fetchNotes();
  }, []);

  const fetchNotes = async () => {
    const querySnapshot = await getDocs(collection(db, 'calendar'));
    const noteData = {};
    querySnapshot.forEach(doc => {
      noteData[doc.id] = doc.data().note;
    });
    setNotes(noteData);
  };

  const handleDateChange = (newDate) => {
    const dateStr = newDate.toISOString().split('T')[0];
    setDate(newDate);
    setCurrentNote(notes[dateStr] || '');
  };

  const handleNoteSubmit = async () => {
    const dateStr = date.toISOString().split('T')[0];
    await setDoc(doc(db, 'calendar', dateStr), { note: currentNote });
    setNotes({ ...notes, [dateStr]: currentNote });
  };

  const handleNoteDelete = async () => {
    const dateStr = date.toISOString().split('T')[0];
    await deleteDoc(doc(db, 'calendar', dateStr));
    const newNotes = { ...notes };
    delete newNotes[dateStr];
    setNotes(newNotes);
    setCurrentNote('');
  };

  const tileClassName = ({ date }) => {
    const dateStr = date.toISOString().split('T')[0];
    return notes[dateStr] ? 'has-note' : '';
  };

  return (
    <div className="calendar-component">
      <h2>Calendar</h2>
      <div className="calendar-container">
        <Calendar onChange={handleDateChange} value={date} tileClassName={tileClassName} />
        <div className="notes-section">
          <textarea value={currentNote} onChange={(e) => setCurrentNote(e.target.value)} placeholder="Add notes or appointments" />
          {currentNote && (
            <>
              <button onClick={handleNoteSubmit}>Save Note</button>
              <button onClick={handleNoteDelete}>Delete Note</button>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

export default CalendarComponent;