import React, { useState, useEffect } from 'react';
import { db } from '../firebase/firebase';
import { collection, getDocs } from 'firebase/firestore';
import './Newsletter.css';

function Newsletter() {
  const [students, setStudents] = useState([]);
  const [selectedEmails, setSelectedEmails] = useState([]);
  const [subject, setSubject] = useState('');
  const [message, setMessage] = useState('');
  const [selectAll, setSelectAll] = useState(false);
  const [allEmails, setAllEmails] = useState('');

  useEffect(() => {
    fetchStudents();
  }, []);

  const fetchStudents = async () => {
    const querySnapshot = await getDocs(collection(db, 'students'));
    const studentData = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
    setStudents(studentData);
    const emails = studentData.map(student => student.email);
    setAllEmails(emails.join(', '));
  };

  const handleSelectAll = () => {
    if (selectAll) {
      setSelectedEmails([]);
    } else {
      setSelectedEmails(students.map(student => student.email));
    }
    setSelectAll(!selectAll);
  };

  const handleEmailChange = (email) => {
    if (selectedEmails.includes(email)) {
      setSelectedEmails(selectedEmails.filter(e => e !== email));
    } else {
      setSelectedEmails([...selectedEmails, email]);
    }
  };

  const handleSendNewsletter = async () => {
    const response = await fetch('http://localhost:3001/send-email', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ subject, message, emails: selectedEmails }),
    });
    const result = await response.json();
    alert(result.message);
  };

  return (
    <div className="newsletter">
      <h2>Send Newsletter</h2>
      <label>Subject:</label>
      <input type="text" value={subject} onChange={(e) => setSubject(e.target.value)} />
      <label>Message:</label>
      <textarea value={message} onChange={(e) => setMessage(e.target.value)}></textarea>
      <label>
        <input type="checkbox" checked={selectAll} onChange={handleSelectAll} />
        Select All
      </label>
      <ul>
        {students.map(student => (
          <li key={student.id}>
            <label>
              <input
                type="checkbox"
                checked={selectedEmails.includes(student.email)}
                onChange={() => handleEmailChange(student.email)}
              />
              {student.firstName} {student.lastName} - {student.email}
            </label>
          </li>
        ))}
      </ul>
      <button onClick={handleSendNewsletter}>Send Newsletter</button>
      <div>
        <h3>All Students' Emails:</h3>
        <textarea value={allEmails} readOnly rows="5" cols="50"></textarea>
      </div>
    </div>
  );
}

export default Newsletter;